import React from 'react';

import classes from './SVG.module.scss';

const svg = ({ pathList, addProp = {} }) => (
  <svg
    xmlns="http://www.w3.org/2000/svg"
    viewBox="0 0 24 24"
    {... addProp}
    className={classes.SVG}
  >
    {pathList}
  </svg>
);

export default svg;
