import React from 'react';

import classes from './Typography.module.scss';

export const H1 = ({ children }) => <h1 className={classes.H1}>{children}</h1>;

export const H2 = ({ children }) => <h2 className={classes.H2}>{children}</h2>;

export const H3 = ({ children }) => <h3 className={classes.H3}>{children}</h3>;

export const H1SubHeading = ({ children }) => (
  <span className={classes.H1SubHeading}>{children}</span>
);

export const BodyText = ({ children }) => (
  <span className={classes.BodyText}>{children}</span>
);

export const FooterText = ({ children }) => (
  <span className={classes.FooterText}>{children}</span>
);

export const IndexPageQuotesAuthorText = ({ children }) => (
  <span className={classes.IndexPageQuotesAuthorText}>{children}</span>
);
